<template>
	<div class="header-main">
		<div class="header-cont">
			<div class="header-about">
				<h2 @click="toIndexPage"><img src="@/assets/house.png" alt="house"></h2>
				<a href="http://wpa.qq.com/msgrd?v=3&uin=2502851992&site=qq&menu=yes" target="_blank"></a>
			</div>
			<div class="header-navs">
				<div v-if="!MYuserInfo" class="login">
					<span></span><a class="r" @click="toAuth"></a>/<a @click="toAuth(1)"></a>
				</div>
				<div v-if="MYuserInfo" class="user-info">
					<h3>{{ MYuserInfo.userName }}</h3>
					<el-dropdown @command="commandChange">
						<span class="el-dropdown-link"><i class="el-icon-arrow-down el-icon--right"></i>
						</span>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item command="/account"></el-dropdown-item>
							<el-dropdown-item command="/account/buyer-order"></el-dropdown-item>
							<el-dropdown-item command="/account/seller-order"></el-dropdown-item>
							<el-dropdown-item command="logout" @click.native="logout" divided></el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
				</div>
				<ul class="navs-list">
					<li></li>
					<li></li>
					<router-link tag="li" to="/release"></router-link>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data () {
		return {
			MYuserInfo: null
		}
	},
	created () {
		this.MYuserInfo = JSON.parse(localStorage.getItem('MYuserInfo'));
	},
	methods: {
		toAuth (i) {
			if (i == 1) {
				this.$router.push({path: '/auth', query: {sign: 1}});
			} else {
				this.$router.push('/auth');
			}
		},
		commandChange (command) {
			if (command != 'logout') {
				this.$router.push({path: command, query: {accountPath : command}});
			}
		},
		logout () {
			this.$api.post('LogOut')
				.then(res => {
					this.$notify({
						title: '',
						message: ''
					});
					localStorage.clear();
					this.$router.push('/auth');
				});
		},
		toIndexPage () {
			this.$router.push('/');
		}
	}
}
</script>

<style lang="scss" scoped>
	.header-main {
		position: fixed;
		z-index: 5;
		top: 0;
		left: 0;
		width: 100%;
		height: 45px;
		background: #292929;
		.header-cont {
			display: flex;
			justify-content: space-between;
			align-items: center;
			flex-wrap: wrap;
			max-width: 1300px;
			margin: 0 auto;
			height: 100%;
		}
	}
	.header-about {
		display: flex;
		align-items: center;
		h2 {
			display: flex;
			align-items: center;
			margin-right: 10px;
			color: #ccc;
			cursor: pointer;
			img {
				margin-right: 5px;
			}
		}
		a {
			padding: 0 12px;
			background: #78c443;
			color: #fff;
			line-height: 22px;
			text-align: center;
			border-radius: 20px;
			cursor: pointer;
		}
	}
	.header-navs {
		display: flex;
		align-items: center;
		.login {
			color: #ccc;
			a {
				color: #ccc;
				cursor: pointer;
			}
			.r {
				margin-left: 5px;
				color: red;
			}
		}
		.user-info {
			display: flex;
			align-items: center;
			h3 {
				padding: 0 20px;
				color: #ccc;
			}
			.el-dropdown {
				color: #ccc;
				cursor: pointer;
			}
			.el-icon--right {
				margin-left: 0;
				font-size: 12px;
				font-weight: bold;
			}
		}
		.navs-list {
			display: flex;
			align-items: center;
			li {
				padding: 0 20px;
				border-right: 1px solid #e4e4e4;
				color: #ccc;
				cursor: pointer;
				&:last-of-type{
					border-right: none;
				}
			}
		}
	}
</style>
