export const upImgUrl = `${baseURL}/apiv2/Info/Other/UpImgFile`; // <<---------->>

let post = (url, data) => {
    return new Promise((resolve, reject) => {
		Axios.post(apiURL[url], data)
			.then(response => {
				if (response.flag) {
					resolve(response);
				}
			})
			.catch(error => {
				reject(error)
			})
	})
}
let postBack = (url, data) => {
    return new Promise((resolve, reject) => {
		Axios.post(apiURL[url], data)
			.then(response => {
				resolve(response);
			})
			.catch(error => {
				reject(error)
			})
	})
}

let get = (url) => {
    return new Promise((resolve, reject) => {
		Axios.get(url)
			.then(response => {
				if (response.flag) {
					resolve(response);
				}
			})
			.catch(error => {
				reject(error)
			})
	})
}

export default {
    post, postBack, get
}
